ación"}},[_c('p',[_vm._v("Edificios inteligentes, equipos ahorradores de energía, utilización de sensores, reducción del impacto energético. Este aspecto clave en la revolución 4.0, busca aplicar, a los procesos repetitivos, mayor seguridad, eficiencia y calidad.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Simulación"}},[_c('p',[_vm._v("Los simuladores permiten conocer “las posibles” tendencias de un sistema; por ejemplo, se puede implementar un modelo de simulación para conocer el gasto energético que puede generar un edificio con toda su infraestructura, antes de comenzar con su edificación; otro uso puede contemplar la utilización de herramientas, equipos, sensores, etc., en ambientes controlados para ver sus “reacciones” y prevenir fallas al momento de pasarlos a producción.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Integración de sistemas"}},[_c('p',[_vm._v("Al integrar diferentes arquitecturas físicas al componente digital, se puede hacer control, seguimiento y administración de toda la plataforma tecnológica, en tiempo real.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"IoT"}},[_c('p',[_vm._v("Son los dispositivos interconectados, digitalmente, que permiten captar datos, procesarlos y convertirlos en información importante; un ejemplo de estos y que está en boga, es la utilización de relojes inteligentes que permiten conocer a su portador, la cantidad de oxígeno en sangre, presión, ritmo cardíaco, etc., cuya información es compartida a través de protocolos entre dispositivos: muy seguramente, al despertar, tendrá un informe en su teléfono móvil sobre la calidad de su sueño. Es de destacar que las ciudades están migrando su desarrollo hacia la conexión, es decir; será más sencillo conocer con anticipación, la ruta del servicio público de transporte, cuál es la vía más rápida para llegar a su destino, calidad del aire, condiciones climáticas, entre otros; todo lo anterior, debido a la posibilidad de conectarse al sistema central de la ciudad y obtener datos en tiempo real; entorno a esto, apunta el futuro en el desarrollo social sostenible.")])])])],1)]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-12 col-lg-7",attrs:{"data-aos":"fade-right"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Ciberseguridad"}},[_c('p',[_vm._v("No se puede contemplar un desarrollo tecnológico sin pensar en la posibilidad de una caída en el sistema, un ataque informático o una fuga de información; al avanzar el desarrollo en la tecnología moderna, también se han aumentado los niveles de protección que permiten resguardar los datos, los cuales se han convertido en el activo fijo más valioso en las organizaciones modernas.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Computación en la nube"}},[_c('p',[_vm._v("Anteriormente, cada organización disponía de su infraestructura tecnológica que permitía contar con servidores dedicados a la prestación de servicios informáticos, ocasionando un elevado consumo eléctrico y aumentando los costos de producción en los procesos; en la actualidad gracias al cloud computing, han surgido empresas dedicadas a la prestación de servicios en la nube, lo que permite a las compañías adquirir los “servicios” a su medida y reducir el consumo de energía eléctrica y requerir, cada vez, de menos espacio dedicado a la infraestructura tecnológica.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Manufactura aditiva"}},[_c('p',[_vm._v("También conocida como impresión en 3D, permite ir agregando capa tras capa de material fundido hasta completar un diseño o una pieza, y reduce costos al poder producir elementos nuevos, o reemplazar los componentes deteriorados en una máquina.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Realidad aumentada"}},[_c('p',[_vm._v("La recreación de espacios en ambientes virtuales ha tenido gran acogida en sectores como la industria y la construcción; la revolución industrial ha permitido conocer un edificio completo antes de su construcción, incluso recorrer sus espacios y ver los detalles como acabados, texturas, iluminación, entre otros.; al utilizar unos lentes especiales, el usuario se ve “sumergido” en una recreación realista de un ambiente lo que le ayudará a tomar la decisión que más se ajuste a sus gustos y exigencias; también las tiendas de compra masiva están usando la realidad aumentada para dar a conocer la previsualización de sus artículos, directamente sobre el espacio donde el cliente desea tenerlo en su hogar; con la utilización de esta nueva tecnología, se volverá cada vez más frecuente la incorporación de gadgets interconectados que permitan conocer más información sobre los productos, la ciudad y su entorno.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Datos masivos, el Big Data"}},[_c('p',[_vm._v("A diario se generan datos que son cargados en sistemas de información online; poder traducir toda esa “cantidad” de datos en información relevante que apoye la toma de decisiones empresariales, se ha convertido en uno de los grandes desafíos de nuestros tiempos. Poder predecir el patrón de comportamiento de los usuarios, la utilización de datos externos para brindar “experiencias” personalizadas a los usuarios, entre otros, son algunos de los muchos usos que se le pueden dar a los datos producidos. El “mal” uso de estos datos ha generado controversia debido a que las grandes compañías venden, comparten o comercializan la información producida por los usuarios, en sus sitios web o diferentes aplicaciones.")])])])],1),_vm._m(7)]),_vm._m(8),_vm._m(9),_c('div',{staticClass:"row"},[_c('div',{staticClass:"px-5 pb-md-3 d-flex"},[_c('div',{staticClass:"col-12 col-xl-7 d-flex pe-4 pe-xl-5 align-items-center flex-column",attrs:{"data-aos":"fade-right"}},[_c('p',{staticClass:"mb-5"},[_vm._v("En el contexto de la automatización industrial, se estudian distintos tipos de algoritmos que permiten optimizar tareas repetitivas mediante procesos estructurados y eficientes. E